body{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    background-color: #ffefd5;
    user-select: none;
    padding: 15px;
    overflow-x:hidden;
    /* @media (max-width:600px) {
        overflow-y:hidden;
    } */
}
textarea::-webkit-scrollbar {
    width: 5px;
}

textarea::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 50px;
}
textarea::-webkit-scrollbar-thumb {
  background-color: #007bff;
  border-radius: 50px;
}
.custom-scrollbar.big{
    width: 100%;
    height: 250px;
}
.custom-scrollbar {
    width: 100px;
    height: 110px;
    overflow: hidden;
  }
  
  .scroll-content {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
  }
  
  /* Стили для кастомной полосы прокрутки в Chrome */
.scroll-content::-webkit-scrollbar {
    width: 5px;
}
@media (max-width: 768px) {
    .scroll-content::-webkit-scrollbar {
        width: 3px;
    }
    textarea::-webkit-scrollbar {
        width: 3px;
    }
}
  
.scroll-content::-webkit-scrollbar-thumb {
    background: #007bff; /* Цвет ползунка */
    border-radius: 50px;

}
  
.scroll-content::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 50px;

}
input{
    user-select: none;
}
.game-box{
    position: relative;
    min-height: 250px;
    max-height: 250px;
    border: solid 2px black;
    padding: 10px;
    overflow: auto;
}
.hiden{
    display: none;
}
.secondomer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 10px;
}
.game-started{
    opacity: 0.3;
}
.game-box__text.active{
    position: absolute;
    top: 10px;
    left: 10px;
}
.tab-box{
    max-width: 1000px;
    margin: 0 auto;
    display: grid;    
    gap: 15px;
    overflow: auto;
    grid-template-columns: 1fr 1fr;
    @media (max-width:600px) {
        grid-template-columns: 1fr;        
    }
}
.time-box{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0.7;
    transition: 0.3s;
}
.time-box.active{
    transition: 0.3s;
    opacity: 1;
    color: rgb(238, 24, 24);

}
.time-box__icon{
    margin-bottom: -6px;
}
.tab-last-best{
    display:flex;
    gap: 100px;
    text-wrap: nowrap;
}
.login-box{
    display:flex;
    justify-content: center;
    align-items: center;
    
}
.login-form{
    display: flex;
    flex-direction: column;
    gap: 30px;
    border: solid 1px red;
    border-radius: 10px;
    padding: 30px;
}
.login-form__box{
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
}
.login-form__title{
    padding: 0;
    margin: 0;
    text-align: center;
}
.user-menu{
    transition: 0.3s;
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding-top: 30px;
    overflow-y: auto;
    padding-bottom: 30px;
    color: wheat;
    transform: translate(0,-100%);
}
.user-menu.active{
    justify-content: center;
    transform: translate(0,0);
    transition: 0.5s;
}
.user-menu__bg{
    transition: 0.3s;
    z-index: -1;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0);
}
.user-menu__bg.active{
    background-color: rgba(0, 0, 0, 0.8);
    opacity: 1;
    transition: 0.5s;
    z-index: 5;

}
.change-name{
    cursor: pointer;
    display: flex;
    align-items: center;
    background: none;
    transition: 0.3s;
    border-radius: 10px;
}
.change-name:hover{
    transition: 0.3s;
    color: blue;
    border-color: blue;
}
#outlined-multiline-flexible{
    position: relative;
}
#outlined-multiline-flexible::before {
    content: "";
    width: 10px; /* Ширина подсветки */
    height: 100%; /* Высота подсветки (по высоте поля ввода) */
    background-color: #007bff; /* Цвет подсветки */
    position: absolute;
    top: 0;
    left: 0;
    transform: translateY(50%); /* Центрирует подсветку по вертикали */
    transition: width 0.2s;
  }  
  #outlined-multiline-flexible:hover::before {
    width: 20px; /* Увеличьте ширину подсветки при наведении */
  }
.pointNone{
    pointer-events: none;
    opacity: 0.6;
}
.lvl-passive{
    opacity: 0.5;
}

.slide-in{
    position:fixed;
    top:0;
    left:0;
    width: 100%;   
    height: 100vh;
    background-color: black;
    transform-origin: bottom;
}
.slide-out{
    position:fixed;
    top:0;
    left:0;
    width: 100%;   
    height: 100vh;
    background-color: black;
    transform-origin: top;
}
.slide-out-img{
    position:fixed;
    top:0;
    left:0;
    width: 100%;   
    height: 100vh;
    transform-origin: top;
}